<template>
  <v-app>
    <v-navigation-drawer v-model="drawer" persistent enable-resize-watcher fixed app>
      <v-list>
        <v-list-tile value="true" v-for="(item, i) in items" :key="i" :to="item.to">
          <v-list-tile-action>
            <v-icon v-html="item.icon" />
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title v-text="item.title" />
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar color="primary" dark app extension-height="7">
      <v-toolbar-side-icon @click.stop="drawer = !drawer" />
      <v-toolbar-title v-text="title" />

      <v-spacer />

      <v-slide-y-transition mode="out-in">
        <router-view name="toolbar" />
      </v-slide-y-transition>

      <v-progress-linear
        v-show="progress.show"
        :indeterminate="progress.indeterminate === true"
        :value="progress.value"
        class="ma-0"
        slot="extension"
        color="accent"
      />

      <v-alert
        v-model="showAlert"
        dismissible
        :type="alert.type"
        class="mt-0 alert"
        transition="slide-y-transition"
        slot="extension"
      >
        {{ alert.text }}
      </v-alert>
    </v-toolbar>
    <v-content>
      <v-slide-y-transition mode="out-in">
        <router-view />
      </v-slide-y-transition>
    </v-content>
  </v-app>
</template>

<script>
export default {
  name: 'App',
  data: () => ({
    drawer: true,
    items: [{
      icon: 'mdi-home',
      title: 'Home',
      to: { name: 'Home' }
    }, {
      icon: 'mdi-map',
      title: 'Canvas',
      to: { name: 'Canvas' }
    }, {
      icon: 'mdi-tune',
      title: 'Mininet Settings',
      to: { name: 'MininetSettings' }
    }, {
      icon: 'mdi-content-save',
      title: 'Export/Import',
      to: { name: 'Export' }
    }, {
      icon: 'mdi-information',
      title: 'About',
      to: { name: 'About' }
    }],
    title: 'Mininet Editor'
  }),
  computed: {
    progress () {
      const working = this.$store.state.working
      return {
        show: !!working,
        indeterminate: working === true,
        value: working.curr != null && working.max != null
          ? working.curr / working.max * 100
          : 0
      }
    },
    alert () {
      return this.$store.state.alert
    },
    showAlert: {
      get () {
        return this.alert.show
      },
      set (value) {
        if (value === false) {
          this.$store.commit('clearAlert')
        }
      }
    }
  }
}
</script>

<style>
html::-webkit-scrollbar {
  width: 0px !important;
}
::selection {
  background: #80CBC4;
}

section {
  margin-bottom: 3em;
}
section > h1,
section > h2,
section > h3,
section > h4,
section > h5,
section > h6 {
  margin-bottom: 16px;
}

.v-toolbar__extension {
  position: relative;
  padding: 0px;
}

.alert {
  position: absolute;
  top: 7px;
  left: 0px;
  right: 0px;
  z-index: -1;
}
</style>
